import Vue from 'vue'
import App from './App.vue'
import router from './router/index'
import store from './store/index'
import Login from "./Login"
import axios from 'axios';
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import components from "@/components"
import filter from "./filters"
Vue.config.productionTip = false
Vue.use(components);
Vue.use(filter);
// 安装
Vue.use(ElementUI);
Vue.prototype.$axios = axios;
//全局前置守卫，判断来访问的路由
router.beforeEach((to,from,next)=>{
  // 判断是否需要登陆
  if(to.meta.isAuthorization){
    if(localStorage.token) next();
    else {
      store.commit("OUT_LOGIN")
    }
    // axios.get("/token")
    //     .then(data=>{
    //         if(localStorage.token) next();
    //         else {
    //             store.commit("OUT_LOGIN")
    //         }
    //     })

  }else{
    next();
  }
})

// 请求拦截
axios.interceptors.request.use(config=>{
  //在发送请求之前做些什么
  store.commit("CHANAGE_IS_LOADING",true);//isloading为true让它进入
  // token
  config.url = "/ele"+config.url;
  if(localStorage.token){
    config.headers={
      authorization:localStorage.token
    }
  }
  return config;
})
// 响应拦截
axios.interceptors.response.use(({data})=>{
  store.commit("CHANAGE_IS_LOADING",false);
  if(data.pageIndex){
    store.commit("CHANGE_PAGEINFO",{pageIndex:data.pageIndex,pageSum:data.pageSum})
  }
  if(data.ok === -2){
    store.commit("OUT_LOGIN")
  }
  return data;
})

new Vue({
  router,
  store,
  render: function (h) { return h(store.state.admin.token?App:Login) }
}).$mount('#app')
