import axios from "axios";
const state = {
    shopTypeList:[],
    allShopTypeList:[],
    shopList:[],
    shopListByType:[]//根据店铺类别获得的店铺信息
}
const mutations = {//同步方法，一般会在异步中通过commit进行同步调用
    CHANGE_SHOP_LIST_BY_TYPE(state,shopList){
      state.shopListByType = shopList
    },
    CHANGE_SHOP_TYPE_LIST(state,shopTypeList){
        state.shopTypeList = shopTypeList;
        // console.log(12321,shopTypeList)
    },
    CHANGE_ALL_SHOP_TYPE_LIST(state,shopTypeList){
        // console.log(shopTypeList)
        state.allShopTypeList = shopTypeList
    },
    CHANGE_SHOP_LIST(state,shopList){
        state.shopList = shopList
    }
}
const actions = {
    getShopListByType({commit}, shopTypeId) {
        axios.get("/shopList/" + shopTypeId)
            .then(data => {
                // console.log(111111111)
                console.log(data)
                commit("CHANGE_SHOP_LIST_BY_TYPE", data.shopList)
            })
    },
    //获取
    getShopTypeList({commit}, params) {
        params = params || {pageIndex: 1}
        axios.get("/shopTypeList", {
            params
        })
            .then(data => {
                // console.log(12121212);
                commit("CHANGE_SHOP_TYPE_LIST", data.shopTypeList);
            })
    },
    //删除
    deleteShopTypeList({commit, dispatch}, id) {
        axios.delete("/shopTypeList/" + id)
            .then(data => {
                // console.log(111111111);
                dispatch("getShopTypeList")
            })
    },

    //获取所有类别
    getAllShopTypeList({commit}) {
        if (state.allShopTypeList.length < 1) {
            axios.get("/allShopTypeList")
                .then(data => {
                    commit("CHANGE_ALL_SHOP_TYPE_LIST", data.shopTypeList)
                })
        }
    },

    getShopList({commit}, params) {
        axios.get("/shopList", {
            params
        }).then(data => {
            commit("CHANGE_SHOP_LIST", data.shopList)

        })
    },
    AddGoodsTypeList({commit}, vm) {//
        axios.post("/addGoodsType", vm.form,)
            .then(data => {
                console.log(data)
            })
    },
    getGoodsListHa({commit}){
        axios.get("/getGoodsList")
            .then(data=>{
                console.log(data)
            })
    }
}


export default {
    state,
    mutations,
    actions
}