const components = {
    pageInfo:()=>import("@/components/common/pageInfo"),
    addShopType:()=>import("@/components/shop/addShopType"),
    addShop:()=>import("@/components/shop/addShop"),
    addGoodsType:()=>import("@/components/goods/addGoodsType"),
    addGoods:()=>import("@/components/goods/addGoods")
}
export default {
    install(Vue){
        for(let key in components){
            Vue.component(key,components[key]);
        }
    }
}