<template>
    <div>
        <div class="toolbar">
            <el-form :inline="true"  class="demo-form-inline">
                <el-form-item >
                    <el-input placeholder="请输入店铺名字"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" >查询</el-button>
                </el-form-item>
                <el-form-item>
                    <el-button type="danger" @click="shopVisible=true" >添加店铺</el-button>
                </el-form-item>
            </el-form>
        </div>
        <el-table
                :data="$store.state.shop.shopList"
                style="width: 100%">
            <el-table-column
                    label="日期"
                    width="180">
                <template slot-scope="scope">
                    <i class="el-icon-time"></i>
                    <span style="margin-left: 10px">{{ scope.row.addTime | date }}</span>
                </template>
            </el-table-column>
            <el-table-column
                    label="店铺名称"
                    width="180">
                <template slot-scope="scope">
                    {{scope.row.shopName}}
                </template>
            </el-table-column>
            <el-table-column
                    label="店铺类别"
                    width="180">
                <template slot-scope="scope">
                    {{scope.row.shopTypeName}}
                </template>
            </el-table-column>
            <el-table-column
                    label="店铺图片"
                    width="180">
                <template slot-scope="scope">
                    <el-image
                            style="width: 100px; height: 100px"
                            :src="scope.row.shopPic | imgUrl"
                            fit="contain"></el-image>

                    <!--<img :src="'http://127.0.0.1/'+scope.row.shopTypePic" alt="">-->
                </template>
            </el-table-column>
            <el-table-column label="操作">
                <template slot-scope="scope">
                    <el-button
                            size="mini"
                           >编辑</el-button>
                    <el-button
                            size="mini"
                            type="danger"
                            >删除</el-button>
                    <el-button
                            @click="goodsTypeVisible=true;shopId=scope.row._id;shopTypeId=scope.row.shopTypeId"
                            size="mini"
                            type="danger"
                    >添加商品类别</el-button>
                </template>
            </el-table-column>
        </el-table>
        <addGoodsType  :shopTypeId="shopTypeId" v-if="goodsTypeVisible" :goodsTypeId="goodsTypeId" :shopId="shopId" :goodsTypeVisible.sync="goodsTypeVisible"></addGoodsType>
        <pageInfo actionsName="getShopList"></pageInfo>
        <addShop v-if="shopVisible" :shopVisible.sync="shopVisible"></addShop>

    </div>
</template>

<script>
    export default {
        name: "shop-list",
        data(){
            return {
                shopVisible:false,
                goodsTypeVisible:false,
                goodsTypeId:"",
                shopId:"",
                shopTypeId:"0"
            }
        }
    }
</script>

<style scoped>

</style>