<template>
    <div>
        <div class="toolbar">
            <el-form :inline="true"  class="demo-form-inline">
                <el-form-item >
                    <el-input placeholder="请输入店铺类别名字"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" >查询</el-button>
                </el-form-item>
                <el-form-item>
                    <el-button type="danger" @click="dialogFormVisible=true" >添加店铺</el-button>
                </el-form-item>
            </el-form>
        </div>

        <el-table
                :border="true"
                :data="$store.state.shop.shopTypeList"
                v-loading="$store.state.isLoading"
                style="width: 100%">
            <el-table-column label="id">
                <template slot-scope="scope">
                    {{scope.row._id}}
                </template>
            </el-table-column>
            <el-table-column label="店铺类别图片">
                <template slot-scope="scope">
                    <el-image
                            style="width: 100px; height: 100px"
                            :src="scope.row.shopTypePic | imgUrl"
                            fit="contain"></el-image>
                </template>
            </el-table-column>
            <el-table-column
                    label="添加时间"
                    width="200">
                <template slot-scope="scope">
                    <i class="el-icon-time"></i>
                    <span style="margin-left: 10px">{{ scope.row.addTime | date}}</span>
                </template>
            </el-table-column>
            <el-table-column
                    label="店铺类别名称"
                    width="180">
                <template slot-scope="scope">
                    {{scope.row.shopTypeName}}
                </template>
            </el-table-column>
            <el-table-column label="操作" width="500px">
                <template slot-scope="scope">
                    <el-button
                            size="mini"
                    >编辑</el-button>
                    <el-button
                            size="mini"
                            type="danger"
                            @click="$store.dispatch('deleteShopTypeList',scope.row._id)">删除</el-button>
                    <el-button @click="AddShop(scope.row._id)"
                               size="mini"
                               type="danger">
                        添加店铺
                    </el-button>
                    <el-button @click="goodsTypeVisible=true;shopTypeId=scope.row._id"
                               size="mini"
                               type="danger">
                        添加商品类别
                    </el-button>
                </template>
            </el-table-column>
        </el-table>
        <pageInfo actionsName="getShopTypeList"></pageInfo>
        <addGoodsType v-if="goodsTypeVisible"  :shopTypeId="shopTypeId" :goodsTypeVisible.sync = "goodsTypeVisible"></addGoodsType>
        <addShop v-if="shopVisible" :shopTypeId="shopTypeId" :shopVisible.sync="shopVisible"></addShop>
        <addShopType :dialogFormVisible.sync="dialogFormVisible"></addShopType>
    </div>
</template>

<script>
    export default {
        name: "shop-type-list",
        data(){
            return {
                goodsTypeVisible:false,
                dialogFormVisible:false,
                shopVisible:false,
                shopTypeId:"0"
            }
        },
        methods:{
            AddShop(id){
                this.shopVisible=true;
                this.shopTypeId=id
                // console.log(this.shopTypeId)
            }
        }
    }
</script>

<style scoped>
    .avatar-uploader .el-upload {
        border: 1px dashed #d9d9d9;
        border-radius: 6px;
        cursor: pointer;
        position: relative;
        overflow: hidden;
    }
    .avatar-uploader .el-upload:hover {
        border-color: #409EFF;
    }
    .avatar-uploader-icon {
        font-size: 28px;
        color: #8c939d;
        width: 178px;
        height: 178px;
        line-height: 178px;
        text-align: center;
    }
    .avatar {
        width: 178px;
        height: 178px;
        display: block;
    }
</style>