<template>
    <div>
        <div class="toolbar">
            <el-form :inline="true" :model="searchForm" class="demo-form-inline">
                <el-form-item >
                    <el-input v-model="searchForm.adminName" placeholder="请输入管理员"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary">查询</el-button>
                </el-form-item>
            </el-form>
        </div>
        <el-table
                :border="true"
                :data="$store.state.admin.adminLog"
                v-loading="$store.state.isLoading"
                style="width: 100%">
            <el-table-column label="id">
                <template slot-scope="scope">
                    {{scope.row._id}}
                </template>
            </el-table-column>
            <el-table-column
                    label="登陆时间"
                    width="180">
                <template slot-scope="scope">
                    <i class="el-icon-time"></i>
                    <span style="margin-left: 10px">{{ scope.row.loginTime }}</span>
                </template>
            </el-table-column>
            <el-table-column
                    label="管理员名称"
                    width="180">
                <template slot-scope="scope">
                    {{scope.row.adminName}}
                </template>
            </el-table-column>
            <el-table-column label="操作">
                <template slot-scope="scope">
                    <el-button
                            size="mini"
                            @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                    <el-button
                            size="mini"
                            type="danger"
                            @click="$store.dispatch('deleteAdminLog',scope.row._id)">删除</el-button>
                </template>
            </el-table-column>
        </el-table>

    </div>
</template>

<script>
    export default {
        name: "adminList",
        data(){
            return{
                searchForm: {
                    adminName:""
                }
            }
        }
    }
</script>

