<template>
    <el-form :model="ruleForm" ref="adminForm" :rules="rules" class="container">
        <h3 style="text-align: center;">修改管理员密码</h3>
        <el-form-item prop="oldPassWord">
            <el-input ref="passWord" v-model="ruleForm.oldPassWord" placeholder="请输入旧密码"></el-input>
        </el-form-item>
        <el-form-item prop = "NewPassWord">
            <el-input ref="NewPassWord" v-model="ruleForm.NewPassWord" type="password" placeholder="请输入新密码"></el-input>
        </el-form-item>
        <el-form-item>
            <el-button :loading="$store.state.isLoading" type="primary" @click="submitForm" style="width:100%">修改</el-button>
        </el-form-item>
    </el-form>
</template>

<script>
    export default {
        name: "up-pass-word",
        data() {
            return {
                ruleForm: {
                    oldPassWord:"",
                    NewPassWord:""
                },
                rules:{
                    oldPassWord: [
                        { required: true, message: '请输入旧密码', trigger: 'blur' },
                        { min: 3, max: 8, message: '长度在 3 到 8个字符', trigger: 'blur' }
                    ],
                    NewPassWord: [
                        { required: true, message: '请输入您的密码', trigger: 'blur' },
                        { min: 3, max: 8, message: '长度在 3 到 8 个字符', trigger: 'blur' }
                    ],
                }
            }
        },
        methods: {
            submitForm(){
                this.$refs.adminForm.validate((isVali)=>{
                    if(isVali){
                        this.$store.dispatch("update",this)
                        this.$router.push({path:'/'})
                    }else {
                        console.log(isVali)
                    }
                })
            }
        }

    }
</script>

<style scoped lang="less">

</style>