<template>
    <div>
        <div class="toolbar">
            <el-form :inline="true"  class="demo-form-inline">
                <el-form-item >
                    <el-input placeholder="请输入商品名字"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" >查询</el-button>
                </el-form-item>
                <el-form-item>
                    <el-button type="danger" @click="addGoods" >添加商品</el-button>
                </el-form-item>
            </el-form>
        </div>
        <el-table
                :data="$store.state.goods.goodsList"
                style="width: 100%">
            <el-table-column
                    label="日期"
                    width="180">
                <template slot-scope="scope">
                    <i class="el-icon-time"></i>
                    <span style="margin-left: 10px">{{ scope.row.addTime | date }}</span>
                </template>
            </el-table-column>
            <el-table-column
                    label="店铺类别"
                    width="180">
                <template slot-scope="scope">
                    {{scope.row.shopTypeName}}
                </template>
            </el-table-column>
            <el-table-column
                    label="店铺名称"
                    width="180">
                <template slot-scope="scope">
                    {{scope.row.shopName}}
                </template>
            </el-table-column>
            <el-table-column
                    label="商品分类"
                    width="180">
                <template slot-scope="scope">
                    {{scope.row.goodsTypeName}}
                </template>
            </el-table-column>

            <el-table-column
                    label="商品名称"
                    width="180">
                <template slot-scope="scope">
                    {{scope.row.goodsListName}}
                </template>
            </el-table-column>
            <el-table-column
                    label="店铺图片"
                    width="180">
                <template slot-scope="scope">
                    <el-image
                            style="width: 100px; height: 100px"
                            :src="scope.row.goodsListPic | imgUrl"
                            fit="contain"></el-image>

                    <!--<img :src="'http://127.0.0.1/'+scope.row.shopTypePic" alt="">-->
                </template>
            </el-table-column>
            <el-table-column label="操作">
                <template slot-scope="scope">
                    <el-button
                            size="mini"
                    >编辑</el-button>
                    <el-button
                            size="mini"
                            type="danger"
                            @click="$store.dispatch('deleteGoodsList',scope.row._id)"
                    >删除</el-button>
                    <el-button
                            @click="goodsTypeVisible=true;shopId=scope.row._id;shopTypeId=scope.row.shopTypeId"
                            size="mini"
                            type="danger"
                    >添加商品类别</el-button>
                </template>
            </el-table-column>
        </el-table>
        <pageInfo actionsName="getGoodsList"></pageInfo>
        <addGoods v-if="goodsVisible" :goodsVisible.sync="goodsVisible"></addGoods>
    </div>
</template>

<script>
     export default {
         name: "goods-list",
         data() {
             return {
                 goodsVisible: false,
                 goodsTypeVisible: false,
                 goodsTypeId:"",
                 shopId:"",
                 shopTypeId:"0"
             }
         },
         methods:{
             addGoods(){
                 this.$store.dispatch("getAllShopTypeList")
                 this.goodsVisible = true

             }
         }

     }
</script>

<style scoped>

</style>

