import axios from "axios";
const state = {//状态
    goodsTypeList:[],
    allGoodsTypeList:[],
    goodsList:[],
    shopListByType:[],//根据店铺类别获得的店铺信息
    goodsListHa:[],//
    addGoodsHa:[],
}
const mutations = {//用来改变状态
    CHANGE_GOODS_TYPE_LIST(state,goodsTypeList){
        state.goodsTypeList = goodsTypeList
    },
    CHANGE_ALL_GOODS_TYPE_LIST(state,{goodsTypeList}){
        console.log(333,goodsTypeList)
        state.allGoodsTypeList = goodsTypeList
    },
    CHANGE_GOODS_LIST(state,goodsList){
        state.goodsList = goodsList
    },
    CHANGE_GOODS_LIST_HAHA(state,goodsList){
        state.goodsListHa = goodsList
    },
    CHANGE_GOODS_HA(state,addGoods){
        state.addGoodsHa = addGoods
    },
    CHANGE_ADD_GOODS_LIST(state,goodsList){
        state.goodsList = goodsList
    }
}
const actions = {
    //商品类别信息
    addGoodsTypeList({commit}, vm) {//添加
        axios.post("/goodsTypeList", vm.form)
            .then(data => {
                console.log(1111, data)
            })
    },
    getGoodsTypeList({commit}, params) {//获取的
        params = params || {pageIndex: 1}
        axios.get("/goodsType", {
            params
        })
            .then(data => {
                commit("CHANGE_GOODS_TYPE_LIST", data.goodsTypeList)
            })
    },
    deleteGoodsType({dispatch}, id) {//删除的
        console.log(id)
        axios.delete("/goodsTypeList/" + id)
            .then(data => {
                dispatch("getGoodsTypeList")
            })
    },
    //商品信息
    getGoodsTypeListById({commit},shopId){//获取 的
        axios.get("/getGoodsTypeById/"+shopId)
            .then(data=>{
                // console.log(222,data)
                if(data.ok === 1){
                    commit("CHANGE_ALL_GOODS_TYPE_LIST",data)
                }
            })
    },
    getGoodsList({commit},params){
        axios.get("/goodsList",{
            params
        }).then(data=>{
            commit("CHANGE_ADD_GOODS_LIST",data.goodsList)
        })
    },
    deleteGoodsList({dispatch},id){
        axios.delete("/goodsList/"+id)
            .then(data=>{
                dispatch("getGoodsList")
            })
    }
}

export default {
    state,
    mutations,
    actions
}