<template>
    <div>
        <div class="toolbar">
            <el-form :inline="true"  class="demo-form-inline">
                <el-form-item >
                    <el-input placeholder="请输入商品类别名字"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" >查询</el-button>
                </el-form-item>
                <el-form-item>
                    <el-button
                            type="danger"
                            @click="goodsTypeVisible=true">添加商品类别</el-button>
                </el-form-item>
            </el-form>
        </div>
        <el-table
            :border="true"
            :data="$store.state.goods.goodsTypeList"
            style="width: 100%">
        <el-table-column label="id">
            <template slot-scope="scope">
                {{scope.row._id}}
            </template>
        </el-table-column>
        <el-table-column
                label="添加时间"
                width="200">
            <template slot-scope="scope">
                <i class="el-icon-time"></i>
                <span style="margin-left: 10px">{{ scope.row.addTime | date}}</span>
            </template>
        </el-table-column>
        <el-table-column
                label="商品名称"
                width="180">
            <template slot-scope="scope">
                {{scope.row.goodsTypeName}}
            </template>
        </el-table-column>
            <el-table-column
                    label="店铺名称"
                    width="180">
                <template slot-scope="scope">
                    {{scope.row.shopListName}}
                </template>
            </el-table-column>
            <el-table-column
                    label="店铺分类"
                    width="180">
                <template slot-scope="scope">
                    {{scope.row.shopTypeName}}
                </template>
            </el-table-column>
        <el-table-column label="操作" width="300px">
            <template slot-scope="scope">
                <el-button
                        size="mini"
                >编辑</el-button>
                <el-button
                        size="mini"
                        type="danger"
                        @click="$store.dispatch('deleteGoodsType',scope.row._id)"
                       >删除</el-button>
            </template>
        </el-table-column>
    </el-table>
        <pageInfo actionsName="getGoodsTypeList"></pageInfo>
        <addGoodsType v-if="goodsTypeVisible"  :shopTypeId="shopTypeId" :goodsTypeVisible.sync = "goodsTypeVisible"></addGoodsType>
    </div>
</template>

<script>
    export default {
        name: "goods-type-list",
        data(){
            return{
                goodsTypeVisible:false,
                shopVisible:false,
                goodsTypeId:"",
                shopId:"",
                shopTypeId:"0"
            }
        },
        mounted() {
            console.log('ceshi')
            this.$store.dispatch("getAllShopTypeList")
            this.$store.dispatch("getGoodsTypeList")
            // console.log(11111,this.shopTypeId,this.shopId);

        },
    }
</script>

