import Vue from 'vue'
import Vuex from 'vuex'
import admin from "./admin"
import goods from "./goods"
import shop from "./shop"
Vue.use(Vuex)

export default new Vuex.Store({
  state:{
    isLoading:false,
    pageInfo:{
      pageIndex:1,
      pageSum:1
    }
  },
  mutations:{
    CHANAGE_IS_LOADING(state,bol){
      state.isLoading = bol;
    },
    CHANGE_PAGEINFO(state,pageInfo){
      state.pageInfo=pageInfo;
    }
  },
  modules:{
    admin,
    goods,
    shop
  }
})
