import axios from "axios";
const state = {
    // token:令牌。初始值。
    token:localStorage.token,//保存登录后的信息  token在登录时发送
    adminName:localStorage.adminName,
    adminLog:[]
}
const mutations = {
    CHANGE_TOKEN(state,{token,adminName}){
        state.token = localStorage.token = token;
        state.adminName = localStorage.adminName = adminName
    },
    OUT_LOGIN(state){
        localStorage.removeItem("token");
        state.token = null;
    },
    CHANGE_ADMIN_LOG(state,adminLog){
        state.adminLog = adminLog;
    }
}
const actions ={
    login({commit},vm){
        axios.post("/login",vm.ruleForm)//这里是login中的 异步接受后，进行内部传值
            .then(data=>{//服务器返回的数据
                // vm.isLoading = false;
                if(data.ok === 1){
                    vm.$message({
                        message:data.msg,
                        type: 'success'
                    });
                    commit("CHANGE_TOKEN",{
                        token:data.token,
                        adminName:data.adminName
                    })
                }

                else{
                    vm.$message.error(data.msg);
                }
            })
    },
    getAdminLog({commit},params){
        axios.get("/adminLog",{
            params
            // headers:{
            //     authorization:localStorage.token
            // }
        })
            .then(data=>{
                commit("CHANGE_ADMIN_LOG",data.adminLog);
                commit("CHANGE_PAGEINFO",{
                    pageIndex:data.pageIndex,
                    pageSum:data.pageSum
                })
            })
    },
    update({commit},vm){
        // console.log(vm)
        axios.put("/adminLog",{
            token:vm.$store.state.admin.token,
            passWord:vm.ruleForm.oldPassWord,
            NewPassWord:vm.ruleForm.NewPassWord
        }).then(data=>{
               console.log(data.msg)
                if(data.ok===1){
                    setTimeout(()=>{
                        vm.$store.commit('OUT_LOGIN')
                        alert("修改成功")
                    },2000)
                }else {
                    alert("修改失败")
                    setTimeout(()=>{
                        vm.$router.push('/upPassWord')
                    },1000)
                }

            })
    },
    deleteAdminLog({commit,dispatch},id){
        axios.delete("/adminLog/"+id)
            .then(data=>{
                // console.log(111111111);
                dispatch("getAdminLog")
            })
    },
}
export default {
    state,
    mutations,
    actions
}