<template>
    <el-form :model="ruleForm" ref="adminForm" :rules="rules" class="container">
        <h3>饿了么后台管理系统</h3>
        <el-form-item prop="adminName">
            <el-input v-model="ruleForm.adminName" placeholder="请输入管理员账号"></el-input>
        </el-form-item>
        <el-form-item prop = "passWord">
            <el-input v-model="ruleForm.passWord" type="password" placeholder="请输入管理员密码"></el-input>
        </el-form-item>
        <el-form-item>
            <el-button :loading="$store.state.isLoading" type="primary" @click="submitForm" style="width:100%">登陆</el-button>
        </el-form-item>
    </el-form>
    <!--
        ruleform中是一个对象
        :rules绑定的规则对应的是script中的rules里面是正则的判断
        prop：是在使用时，eleui表单定义的必须要要写的，名字需要和下面绑定的一样
        ：loading ：是用来判断用户是否登录成功  true为成功  false为失败
    <div>-->
    <!--登陆-->
    <!--<el-button>默认按钮</el-button>-->
    <!--<input type="button" value="登陆" @click="$store.commit('CHANGE_TOKEN','123456')">-->
    <!--</div>-->
</template>

<script>
    export default {
        name: "login",
        data(){
            return {
                // isLoading:false,
                ruleForm:{//返回的是对象，对应的是上方的ruleform
                    adminName:"",
                    passWord:""
                },
                rules:{//规则判断输入内容是否符合下面的定义规则
                    adminName: [
                        { required: true, message: '请输入账号名称', trigger: 'blur' },
                        { min: 3, max: 9, message: '长度在 3 到 5 个字符', trigger: 'blur' }
                    ],
                    passWord: [
                        { required: true, message: '请输入您的密码', trigger: 'blur' },
                        { min: 3, max: 8, message: '长度在 3 到 8 个字符', trigger: 'blur' }
                    ]
                }
            }
        },
        methods:{
            submitForm(){//点击登录时调用的方法
                this.$refs.adminForm.validate((isVali)=>{//vue结合element-ui进行表单验证，其中refs对应上方的ref标识。这是一种固定的方法
                    if(isVali){// 通过isvali的特性进行验证，true为成功如果验证成功
                        // this.isLoading = true;// 开始进行登陆
                        this.$store.dispatch("login",this)//将输入的信息反馈通过接口进行返回
                        // console.log("我看看你是什么",this)
                    }else{
                        console.log(isVali);
                    }
                })
            }
        }
    }
</script>

<style scoped lang="less">
    .container{
        width:350px;
        margin:250px auto;
        border:1px solid lightskyblue;
        padding:10px 30px;
        h3{
            text-align:center;
        }
    }
</style>